* {
  margin: 0;
}

.star__item {
  width: 16px;
  height: 16px;
  background: url("images/star.svg") center center/auto no-repeat;
}

.star__item:not(:last-child) {
  margin-right: 4px;
}

.stars {
  display: flex;
}

.stars--1 :first-child,
.stars--2 :nth-child(-n + 2),
.stars--3 :nth-child(-n + 3),
.stars--4 :nth-child(-n + 4),
.stars--5 :nth-child(-n + 5) {
  background-image: url("images/star-active.svg");
}
